import React, {Component} from 'react';
import * as d3 from 'd3';
import './Hist.css'

// import hdata from './hdata'


class Hist extends Component {
    state = {
        width: 900,
        height: 500,
        timeOut: 750,
        animateOut: 700,
        animateIn: 750,
        margin: {
            top: 40,
            right: 40,
            bottom: 120,
            left: 105,
        },
        toolTip: false,
        bins: 20,
    };
    offset = 100;
    barWidthOffset = this.state.width - this.offset;
    graphWidth = this.state.width - this.state.margin.left - this.state.margin.right;
    graphHeight = this.state.height - this.state.margin.top - this.state.margin.bottom;


    draw(svg, data) {
        // data = hdata;
        // console.log("Max", d3.max(data, d => d.count));
        const graph = svg.append('g')
            .attr('width', this.graphWidth)
            .attr('height', this.graphHeight)
            .attr('transform', `translate(${this.state.margin.left}, ${this.state.margin.top})`);


        const x = d3.scaleLinear()
            .domain([20, 200])
            .range([0, this.graphWidth]);
        graph.append("g")
            .attr("id", "xlist")
            .attr("transform", "translate(0," + this.graphHeight + ")")
            .call(d3.axisBottom(x))
            .style("font-size", "14")
            .attr('fill', 'white');

        let histogram = d3.histogram()
            .value(d => d.count)
            .domain(x.domain())
            .thresholds(this.state.bins);

        let self = this;
        let bins = histogram(data);
        let max = d3.max(bins, d => d.length);
        const y = d3.scaleLinear()
            .domain([0, d3.max(bins, d => d.length)])
            .range([this.graphHeight, 0]);
        graph.append("g")
            .attr("id", "ylist")
            .call(d3.axisLeft(y))
            .style("font-size", "14")
            .attr('fill', 'orange');


        // console.log('bins', bins);
        // rects.attr('width', x.bandwidth)
        //     .attr('height', d => this.graphHeight - y(d.count))
        //     .attr('fill', 'orange')
        //     .attr('x', d => x(d.location))
        //     .attr('y', d => y(d.count));
        // let self = this;
        // graph.selectAll('rect')
        //     .data(data)
        //     .enter()
        //     .append('rect')
        //     .attr('width', x.bandwidth)
        //     .attr('fill', 'orange')
        //     .attr('x', d => x(d.location))
        //     .attr('y', this.graphHeight)
        //     .attr('height', 0)
        // .on('mouseover', function (d) {
        //     // console.log("Here");
        //
        //     toolTip.transition()
        //         .duration(700)
        //         .style("opacity", 1);
        //     toolTip.html(d.count)
        //         .style("left", x(d.location) + 570 + "px")
        //         .style("top", y(d.count) + 270 + "px");
        //
        //     console.log(x.bandwidth());
        //     // bar Expand
        //     d3.select(this)
        //         .transition()
        //         .ease(d3.easeCubicInOut)
        //         .duration(500)
        //         .attr('height', d => self.graphHeight - y(d.count) + 10)
        //         .attr('y', d => y(d.count) - 10)
        //         .attr('width', x.bandwidth() + 5)
        //         .attr('x', d => x(d.location) - 5 / 2)
        //         .attr('fill', 'red');
        // })
        // .on('mouseleave', function (d) {
        //     // console.log("Here");
        //     toolTip.transition()
        //         .duration(500)
        //         .style("opacity", 0);
        //
        //     // bar Shrink
        //     d3.select(this)
        //         .transition()
        //         .ease(d3.easeCubicInOut)
        //         .duration(500)
        //         .attr('height', d => self.graphHeight - y(d.count) - 10)
        //         .attr('y', d => y(d.count) + 10)
        //         .attr('width', x.bandwidth() - 5)
        //         .attr('x', d => x(d.location) + 5 / 2)
        //         .attr('fill', 'orange');
        //
        // })
        // .transition()
        // .ease(d3.easeCubicInOut)
        // .duration(this.state.animateIn)
        // .attr('height', d =>  this.graphHeight - y(d.price))
        // .attr('y', d => y(d.price));

        graph.selectAll("rect")
            .data(bins)
            .enter()
            .append("rect")
            .attr("transform", function (d) {
                return "translate(" + x(d.x0) + "," + y(d.length) + ")";
            })
            .attr("x", 1)
            .attr('y', this.graphHeight)
            .attr("height", 0)
            .attr("width", function (d) {
                return x(d.x1) - x(d.x0) - 1;
            })
            // .on('mouseover', function (d) {
            //     self.state.bins += 1;
            //     console.log("MouseOver", self.state.bins);
            //     self.renderAgain(self.props.data.data);
            // })
            .on("mousedown", function () {
                // this.pauseEvent();
                let div = d3.select(this);
                // .classed("Active", true)
                let xPos = d3.mouse(div.node())[0];
                let w = d3.select(window)
                    .on("mousemove", mousemove)
                    .on("mouseup", function () {
                        w.on("mousemove", null).on("mouseup", null);
                    });

                function mousemove() {
                    if (d3.mouse(div.node())[0] + 20 < xPos && self.state.bins > 1) {
                        self.state.bins -= 1;
                        self.renderAgain(self.props.data.data);
                        xPos = d3.mouse(div.node())[0];
                    } else if (d3.mouse(div.node())[0] - 20 > xPos && self.state.bins < 25) {
                        self.state.bins += 1;
                        self.renderAgain(self.props.data.data);
                        xPos = d3.mouse(div.node())[0];
                    }
                }
            })
            // .transition()
            // .ease(d3.easeCubicInOut)
            // .duration(this.state.animateIn)
            .attr('y', d => y(max))
            .attr("height", function (d) {
                return self.graphHeight - y(d.length);
            })
            .attr('fill', 'orange');


        // const toolTip = d3.select('.toolTip')
        //     .style("opacity", 0);

        // create and call the axes
        // const xAxis = d3.axisBottom(x);
        // const yAxis = d3.axisLeft(y)
        //     .ticks(3)
        //     .tickFormat(d =>"#" + d);
        //
        // xAxisGroup.call(xAxis);
        // yAxisGroup.call(yAxis);
        //
        // yAxisGroup.call(yAxis).selectAll('text')
        //     .style("font-size", "14")
        //     .attr("transform", "rotate(0)");
        //
        // xAxisGroup.selectAll('text')
        //     .style("font-size", "14")
        //     .attr('x', '-10')
        //     .attr('y', '-5')
        //     .attr('transform', 'rotate(-45)')
        //     .attr('text-anchor', 'end')
        //     .attr('fill', 'white');
        //

        // y label title
        graph
            .append("text")
            .attr("transform", "rotate(-90)")
            .attr("y", 0 - self.state.margin.left)
            .attr("x", 0 - self.state.height / 3)
            .attr("dy", "1em")
            .style("text-anchor", "middle")
            .style("font-size", "35")
            .style("z-index", "18")
            .attr('fill', 'white')
            .text(`${self.props.data.yTitle}`);

        // x label title
        d3.select('.main')
            .append("text")
            .style("left", '45%')
            .style("position", 'absolute')
            .style("top", '70%')
            .style("text-anchor", "middle")
            .style("font-size", "50 !important")
            .text(`${self.props.data.xTitle}`)
            .append("text")
            .attr("id", "binSize")
            .text(`Bin Size: ${self.state.bins}`);

        // Chart Title
        d3.select('.main')
            .append("text")
            .style("left", '40%')
            .style("position", 'absolute')
            .style("top", '15%')
            .style("text-anchor", "middle")
            .style("z-index", "18")
            // .style("text-decoration", "underline")
            .text(`${self.props.data.graphTitle}`);
    }

    drawChart() {
        let svg = d3.select(".main")
            .append('svg')
            .attr("width", this.state.width)
            .attr("height", this.state.height);

        this.draw(svg, this.props.data.data);
    }

    renderAgain(data) {
        let svg;
        // const delay = transition().duration(500)
        // d3.selectAll("rect")
        //     .transition()
        //     .duration(this.state.animateOut)
        //     .attr("y", this.graphHeight)
        //     .attr("height", 0);

        setTimeout(() => {
            d3.selectAll("svg")
                .remove();
            d3.select(".main")
                .remove();
        }, this.state.timeOut);

        setTimeout(() => {
            svg = d3.select(".App")
                .append("div")
                .attr("class", "main")
                .append('svg')
                .attr("width", this.state.width)
                .attr("height", this.state.height);
            d3.select(".main")
                .append("div")
                .attr("class", "toolTip");
            this.draw(svg, data);
        }, this.state.timeOut);
    }

    shouldComponentUpdate() {
        return false
    }

    UNSAFE_componentWillReceiveProps(nextProps) {
        // we have to handle the DOM ourselves now
        if (nextProps.data.data !== this.props.data.data) {
            this.renderAgain(nextProps.data.data)
        }
    }

    componentDidMount() {
        console.log("From Hist", this.props.data.data);
        let svg = d3.select(".main").append('svg').attr("width", this.state.width).attr("height", this.state.height);
        this.draw(svg, this.props.data.data);
    }

    render() {
        this.drawChart();
        return (
            <div className="main">
                <div className="toolTip">
                </div>
            </div>
        );
    }

}


export default Hist;
