import React, {useEffect} from 'react';
import * as d3 from 'd3';
import './App.css';
import Menu from "./Menu";
import Hist from "./Hist";
import Main from "./Main";
import Footer from "./Footer";
import {Spring} from "react-spring/renderprops-universal";
import data from './data.json'

const dataObj = [
    {
        data: [],
        graphTitle: "Michelin Star Restaurants' Distribution",
        xTitle: "Cities",
        yTitle: "Number of Restaurants",
        avg: 0,
    },
    {
        data: [],
        graphTitle: "Restaurants Ratings in New York",
        xTitle: "Restaurants",
        yTitle: "Rating",
        avg: 0,
    },
    {
        data: [],
        graphTitle: "Restaurants Ratings in San Francisco",
        xTitle: "Restaurants",
        yTitle: "Rating",
        avg: 0,
    },
    {
        data: [],
        graphTitle: "Restaurants Ratings in London",
        xTitle: "Restaurants",
        yTitle: "Rating",
        avg: 0,
    },
    {
        data: [],
        graphTitle: "Restaurants Ratings in Tokyo",
        xTitle: "Restaurants",
        yTitle: "Rating",
        avg: 0,
    },
    {
        data: [],
        graphTitle: "Restaurant's Work-Force Histogram",
        xTitle: "Number of Workers",
        yTitle: "Number of Restaurants",
        avg: 0,
    },
];

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            menuItems: ['Worldwide', 'New York', 'San Francisco', 'London', 'Tokyo', 'Workforce'],
            currentFocus: 0,
        };
    }

    updateCurrentFocus(i) {
        this.setState({
            currentFocus: i
        })
    }

    getCityCountFromData() {
        // console.log(data.length);
        let d = {};
        let jsonData = [];
        let location;
        let sum = 0, count = 0;
        for (let i = 0; i < data.length; i++) {
            location = data[i].Location;
            location = location[0].toUpperCase() + location.slice(1, location.length);
            // console.log(location);
            if (!d[location]) {
                d[location] = 1;
            } else {
                d[location] += 1;
            }
            if (typeof (data[i].GRating) === "number") {
                count += 1;
                sum += data[i].GRating;
            }
        }

        for (let key in d) {
            jsonData.push({location: key, count: d[key]})
        }
        dataObj[0].avg = (sum / count).toFixed(2);

        return jsonData;
    }

    getNYData(index) {
        let place = ['', 'New york', 'San francisco', 'London', 'Tokyo'];
        let d = {};
        let jsonData = [];
        let location;
        let sum = 0, count = 0;
        // console.log(place[index]);
        for (let i = 0; i < data.length; i++) {
            location = data[i].Location;
            location = location[0].toUpperCase() + location.slice(1, location.length);
            if (location === place[index]) {
                d[data[i].Hotel] = data[i].GRating;
                count += 1;
                sum += data[i].GRating;
                // console.log(d[data[i].Hotel] = data[i].GRating, data[i].Hotel);
            }
            dataObj[index].avg = (sum / count).toFixed(2);
        }


        for (let key in d) {
            jsonData.push({location: key, count: d[key]})
        }
        return jsonData;

    }

    getWorkForce(index) {
        let d = {};
        let jsonData = [];

        for (let i = 0; i < data.length; i++) {
            d[i] = data[i]['Work-Force'];
        }

        for (let key in d) {
            jsonData.push({location: 20 + parseInt(key), count: d[key]})
        }
        // console.log(jsonData);
        return jsonData;
    }


    showHistogram() {
        return this.state.menuItems[this.state.currentFocus] === "Workforce";
    }

    render() {
        dataObj[0].data = this.getCityCountFromData();
        dataObj[1].data = this.getNYData(1);
        dataObj[2].data = this.getNYData(2);
        dataObj[3].data = this.getNYData(3);
        dataObj[4].data = this.getNYData(4);
        dataObj[5].data = this.getWorkForce(5);
        // console.log(dataObj[4].avg);
        // console.log(this.state.currentFocus, dataObj[1].data);
        return (
            <div>
                <div className="App">
                    {!this.showHistogram() && <Main data={dataObj[this.state.currentFocus]}/>}
                    {this.showHistogram() && <Hist data={dataObj[this.state.currentFocus]}/>}
                    <Menu menuItems={this.state.menuItems} updateFocus={this.updateCurrentFocus.bind(this)}/>
                    {!this.showHistogram() && <Footer data={dataObj[this.state.currentFocus].avg}/>}
                </div>
            </div>
        );
    };
}


export default App;
