import React, { useState } from 'react';
import './Menu.css';


class Menu extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            focusClass: this.props.menuItems,
            focusedIndex: null,
        }
    }


    setFocus(i){
        this.setState({
            focusedIndex: i,
        })
    }

    getClassName(i){
        if(i === this.state.focusedIndex){
            return 'focusedClass'
        }
        return null;
    }

    render() {
        return (
            <div className="menu-bar">
                <h3>
                    Menu
                </h3>
                <ul className="listStyle">
                    {this.props.menuItems.map((item, i) => (
                        <li
                            key={item}
                            className={this.getClassName(i)}
                            onClick={() => {
                                this.setFocus(i);
                                this.props.updateFocus(i);
                            }}>{item}</li>
                    ))}
                </ul>
            </div>
        );
    }
}
export default Menu;
